import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Card, Row, Col, Popconfirm, Tooltip, Checkbox, Table} from 'antd';
import UploadDraggerModal from './UploadModel'
import AddOrEditorModel from './AddOrEditorModel'
import { hostUrl } from '../../../../../env'
import './pushconfig.less'
// import {setLocal,getLocal} from '../../../../../utils'
const warnModules = [
    { label: '失联预警', value: 'alarmMiss' },
    { label: '网络预警', value: 'alarmNetwork' },
    { label: '挂科预警', value: 'alarmFail' },
    { label: '消费预警', value: 'alarmConsume' },
    { label: '夜归预警', value: 'alarmBack' },
    { label: '逃课预警', value: 'alarmCourse' },
];
const warnModules2 = [
    { label: '红色预警', value: 'redLevel' },
    { label: '橙色预警', value: 'orangeLevel' },
    { label: '黄色预警', value: 'yellowLevel' },
    { label: '蓝色预警', value: 'blueLevel' },
];

class PushConfig extends Component {    
    state = {
        pageNum: 1,
        pageSize: 10,
        dataLists: [],
        isEdit: false,
        editData: undefined,
        visible: false,
        isImport: false        
    }

    componentWillReceiveProps(nextProps) {
        if(JSON.stringify(nextProps) !== JSON.stringify(this.props)){            
            this.setState({
                dataLists:nextProps.dataSource,
            })
        }
    }    
    changePage = (pagination) => {
        this.setState({pageNum:pagination.current,pageSize:pagination.pageSize})
    }
    deleteAction = (record) => {
        let dataLists = this.props.dataSource;
        dataLists.splice(record.index - 1, 1)
        this.props.onChange({
            alarmPushList: dataLists
        })
    }
    // 编辑单条
    handleEdit=(record)=>{
        this.setState({
            editData:record,
            visible:true,
            isEdit:true
        })
    }
    addOrEditAction = (values,tag) => {
        // let { jobNumber, pushName, pushMail, warnModule, warnModule2,pushPhone } = values;
        let {values:{warnModule, warnModule2},pushPerson} = values;        
        pushPerson.map(data=>{
            let warnArr = [], warnName,warnArr2 = [], warnName2;
            warnModules.forEach(ele => {
                if (warnModule.some(a => a === ele.value)) {
                    warnArr.push(ele.label);
                    data[ele.value] = 1;
                } else {
                    data[ele.value] = 0;
                }
            })
            warnModules2.forEach(ele => {
                if (warnModule2.some(a => a === ele.value)) {
                    warnArr2.push(ele.label);
                    data[ele.value] = 1;
                } else {
                    data[ele.value] = 0;
                }
            })
            warnName = warnArr.join(',');
            warnName2 = warnArr2.join(',');
            data.warnName = warnName;
            data.warnName2 = warnName2;
            return data
        })
        
        
        let dataLists = this.props.dataSource;
        // if (this.state.isEdit) {
        //     let isError = false;
        //     dataLists.forEach((item, index) => {
        //         if (item.key === this.state.editData.key) {
        //             if (dataLists.some(a => {
        //                 return (a.key !== this.state.editData.key) && (a.jobNumber === data.jobNumber || a.pushMail === data.pushMail);
        //             })) {
        //                 isError = true;
        //                 message.error('预警推送对象或邮箱重复！');
        //             } else {
        //                 dataLists[index] = data;
        //             }
        //         }
        //     })
        //     if (isError) {
        //         return;
        //     }
        // } else {
        //     if (dataLists.some(item => {
        //         return item.jobNumber === data.jobNumber || item.pushMail === data.pushMail;
        //     })) {
        //         message.error('预警推送对象或邮箱重复！');
        //         return;
        //     }
        //     dataLists.push(data);
        // }
        //将点击关闭弹窗后关联级别的数据保存到localstorage
        let newData={
            ...this.state.editData,
            warnModule2
        }
        // 处理一下编辑模式和新增模式合并数据的不同点
        if(tag){
            // 从编辑模式返回的数据,先删除原有的再插入编辑后的对象
            dataLists=dataLists.filter(item=>item.jobNumber !== pushPerson[0].jobNumber)
            console.log(dataLists)
            dataLists=[...dataLists,...pushPerson] 
        }else{
            dataLists=[...dataLists,...pushPerson] 
        }
        dataLists=dataLists.map(item=>{
            item['mailStatus']=newData['mailStatus'] || 0
            item['msgStatus']=newData['msgStatus'] || 0
            return item
        })
        // setLocal('edit',JSON.stringify(newData))
        this.setState({ visible: false, isEdit: false});
        this.props.onChange({
            alarmPushList: dataLists
        })
    }
    importAction = (data) => {
        this.setState({isImport:false},()=>{
            let alarmPushLists=[];
            data.forEach(item=>{
                let { jobNumber, pushName, pushMail, alarmMiss, alarmFail, alarmNetwork, alarmConsume,alarmCourse,alarmBack,mailStatus,msgStatus} = item;
                let warnArr=[],warnName,warnModule=[];
    
                if(alarmMiss){
                    warnModule.push('alarmMiss');
                    warnArr.push('失联预警');
                }
                if(alarmNetwork){
                    warnModule.push('alarmNetwork');
                    warnArr.push('网络预警');
                }
                if(alarmFail){
                    warnModule.push('alarmFail');
                    warnArr.push('挂科预警');
                }
                if(alarmConsume){
                    warnModule.push('alarmConsume');
                    warnArr.push('消费预警');
                }
                if(alarmCourse){
                    warnModule.push('alarmCourse');
                    warnArr.push('逃课预警');
                }
                if(alarmBack){
                    warnModule.push('alarmBack');
                    warnArr.push('夜归预警');
                }
                warnName = warnArr.join(',');
                let d = {jobNumber, pushName, pushMail, alarmMiss, alarmFail, alarmNetwork, alarmConsume,alarmCourse,alarmBack,warnName,warnModule,mailStatus,msgStatus}
                alarmPushLists.push(d)
            })
            // console.log(alarmPushLists);
            this.props.onChange({
                alarmPushList: [...this.props.dataSource,...alarmPushLists],
            })
        });
    }
    cancelAddOrEdit = () => {
        this.setState({
            visible: false,
            isEdit: false, editData: undefined
        })
    }

    //推送方式
    pushTypeChange = (checkedValues) => {
        let pushObj = [{label: '短信推送',value: 'msgStatus'},{label: '邮件推送',value: 'mailStatus'}]
        let pushData = {};
        pushObj.forEach(ele=>
            {
                if (checkedValues.some(a => a === ele.value)) {
                    pushData[ele.value] = 1;
                } else {
                    pushData[ele.value] = 0;
                }
            })
        this.props.onChange({
            alarmPushList: [...this.props.dataSource].map(item=>({...item,...pushData})),
        })
    }
    render() {
        let columns = [{
            title: '序号',
            dataIndex: 'index',
            width: "8%",
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }, {
            title: '工号',
            dataIndex: 'jobNumber',
            width: "15%",
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }, {
            title: '姓名',
            dataIndex: 'pushName',
            width: "15%",
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }
        , {
            title: '手机号',
            dataIndex: 'pushPhone',
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }, {
            title: '邮箱',
            dataIndex: 'pushMail',
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }, {
            title: '提醒模块',
            dataIndex: 'warnName',
            width: "15%",
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        },
        {
            title: '关联级别',
            dataIndex: 'warnName2',
            width: "15%",
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }
        , {
            title: '操作',
            dataIndex: 'handle',
            render: (text, record) => (
                <div title = {text}>
                    <a style={{ color: '#309cff', marginRight: 10 }} onClick={() => {
                        this.handleEdit(record)
                    }} href={'javascript:void(0)'}>编辑</a>
                    <Popconfirm title="确认删除?" onConfirm={() => this.deleteAction(record)}>
                        <a href="javascript:;" style={{ color: '#8d99b0' }}>删除</a>
                    </Popconfirm>
                </div>
            )
        }
    ];
        // let pageNum = this.state.pageNum;
        // let pageSize = this.state.pageSize;
        // const dataSource = this.props.dataSource.filter((item, index) => {
        //     if ((index + 1) > (pageNum - 1) * pageSize && (index + 1) <= pageNum * pageSize) {
        //         return true;
        //     } else {
        //         return false;
        //     }
        // });
        const dataSource = this.props.dataSource
        let pushTypeData = [];
        if(dataSource[0]&&dataSource[0].msgStatus===1){
            pushTypeData.push('msgStatus');
        }
        if(dataSource[0]&&dataSource[0].mailStatus===1){
            pushTypeData.push('mailStatus');
        }
        // console.log(dataSource)
        return (
            <Card
                title="预警推送人及推送方式"
                className={this.props.className + ' earlywarn-push-config'} style={this.props.style}>
                <Row className="pushType">
                    <p className="title">推送方式：</p>
                    <Checkbox.Group style={{ width: '100%' }} onChange={this.pushTypeChange} value={pushTypeData} disabled={dataSource.length===0}>
                        <Checkbox value={'msgStatus'}>短信推送</Checkbox>
                        <Checkbox value={'mailStatus'}>邮件推送</Checkbox>
                    </Checkbox.Group>
                </Row>
                <div className="hr" />
                <Row style={{ margin: '-5px 0px' }} className="pushPerson">
                    <Col span={12}>
                        <p className="title">推送人员：</p>
                    </Col>
                    <Col span={12} style={{ textAlign: 'right' }}>
                        <Tooltip placement="top" title="添加" overlayClassName={'common-tooltip'}>
                            <span style={{ marginRight: 12, fontSize: 20 }}
                                className="iconfont icon-btn icon-tianjiachengyuan" onClick={() => this.setState({ visible: true })}></span>
                        </Tooltip>                        
                        {this.props.editorId ? (
                            <Tooltip placement="top" title="导出" overlayClassName={'common-tooltip'} onClick={() => {
                                //导出
                                window.open( hostUrl+`/bigdata/alarmStrategy/downloadAlarmPushData?id=${this.props.editorId}`);
                                // message.error('无导出链接！')
                            }}>
                                <span style={{ marginRight: 12 }} className="iconfont icon-btn icon-wenjiandaochu"></span>
                            </Tooltip>
                        ) : null}
                        <Tooltip placement="top" title="导入" overlayClassName={'common-tooltip'} onClick={() => this.setState({ isImport: true })}>
                            <span style={{ marginRight: 12 }} className="iconfont icon-btn icon-wenjiandaoru"></span>
                        </Tooltip>
                        <Tooltip placement="top" title="下载" overlayClassName={'common-tooltip'}>
                            <span style={{ fontSize: 20 }} className="iconfont icon-btn icon-msnui-download" onClick={() => {
                                //下载
                                window.location.href = hostUrl+'/bigdata/alarmStrategy/downloadAlarmPushTemplate';
                                // message.error('无下载链接！')
                            }}></span>
                        </Tooltip>
                    </Col>
                </Row>
                <Row style={{ marginTop: 14 }}>
                    <Table dataSource={dataSource}
                        columns={columns}
                        pageSize={this.state.pageSize}
                        size="middle"
                        pagination={{
                            size: "small",
                            current:this.state.pageNum,
                            total:this.state.total,
                            showQuickJumper: true,
                            showTotal: total => `共 ${total} 条数据`
                        }}
                        bordered
                        onChange={this.changePage.bind(this)} />
                </Row>
                {this.state.visible ? (
                    <AddOrEditorModel
                        title={this.state.isEdit ? '编辑推送对象' : '新增推送对象'}
                        visible={this.state.visible}
                        onCancel={this.cancelAddOrEdit}
                        onOk={this.addOrEditAction}
                        warnModules={warnModules}
                        warnModules2={warnModules2}
                        isEdit={this.state.isEdit}                        
                        editData={this.state.editData}
                        dataLists={this.state.dataLists}                    
                    />) : null}
                {this.state.isImport ? (
                    <UploadDraggerModal
                        visible={this.state.isImport}
                        action={'/bigdata/alarmStrategy/uploadAlarmPushData'}
                        onCancel={() => this.setState({ isImport: false })}
                        onOk={this.importAction}
                    />
                ) : null}
            </Card>
        )
    }
}

export default withRouter(PushConfig);


