import React, {Component} from 'react'
import {Row, Col, message, Checkbox} from 'antd'
import MainModal from '../../../../common/components/MainModal'
import '../styles.less'
import {getAction, postAction} from '../../../../axios';

const CheckboxGroup = Checkbox.Group;

class EarlyWarningObjModel extends Component {
    state = {
        dataLists: [],
        checkAll: false,
        indeterminate: false,
    };

    componentWillMount() {
        this.setState({
            dataLists:JSON.parse(JSON.stringify(this.props.dataLists)) || []
        })
    }

    // 点击单个或多个学院
    selectData = (collegeCode, index) => {
        let data = {strategyTarget: collegeCode};
        postAction("/bigdata/alarmStrategy/checkCollegeHasAlarmStrategy", data).then(
            (res) => {
                if (res.success) {
                    if (res.obj) {
                        message.warn('学院已有预警策略配置');
                    } else {
                        let dataLists = this.state.dataLists;
                        dataLists[index].active = !dataLists[index].active;
                        let arrLen = this.state.dataLists.filter(item => !item.active).map(a => {
                            return a;
                        });
                        if (arrLen.length > 0) {
                            this.setState({dataLists,indeterminate:true,checkAll:false})
                        }else {
                            this.setState({dataLists,indeterminate:false,checkAll:true})
                        }
                    }
                }
            }
        )
    }

    // 保存按钮
    onOk = () => {
        let slecteds = this.state.dataLists.filter(item => item.active).map(a => {
            return a.collegeCode;
        }).join(',');
        this.props.onOk(slecteds);
    }

    // 全选事件
    onCheckAllChange = (e) => {
        // 后台返回已存在预警学院
        let dataLists = this.state.dataLists;
        getAction(
            "/bigdata/alarmStrategy/getHasAlarmCollege",
            {
                id:this.props.editorId || ''
            }
        ).then(
            (res) => {
                if (res.success) {
                    if (res.obj && res.obj.length > 0) {
                        let hasDone=res.obj.join().split(',')
                        // 根据已有预警策略过滤可选对象
                        dataLists=dataLists.map(item=>{
                            if(hasDone.indexOf(item.collegeCode)>-1){
                                item.active=false
                            }
                            return item
                        })
                        this.setState({
                            dataLists
                        })
                        message.warn('学院已有预警策略配置');
                    } else {
                        dataLists.active = !dataLists.active;
                    }
                }
            }
        )

        // 页面全选学院
        let activeData = this.state.dataLists;
        if (e.target.checked){
            activeData.forEach(res => {
                if(!res.active) {
                    res.active = !res.active;
                }
            });
            this.setState({activeData,checkAll: e.target.checked,indeterminate: false})
        } else {
            activeData.forEach(res => {
                if(res.active) {
                    res.active = !res.active;
                }
            });
            this.setState({activeData,checkAll:false,indeterminate:false})
        }

        // 页面全选和后台返回的两组数组去重
        // function diff( dataLists, activeData) {
        //     for (var i = 0; i < dataLists.length; i++) {
        //         for (var j = 0; j < activeData.length; j++) {
        //             if (dataLists[i] === activeData[j]) {
        //                 dataLists.splice(i, 1)
        //             }
        //         }
        //     }

        //     for (var k = 0; k < activeData.length; k++) {
        //         dataLists.push(activeData[k]);
        //     }
        //     return dataLists
        // }
    };


    render() {
        // 弹框里面的学院遍历
        let dataLists = this.state.dataLists.map((item, index) => (
            <Col span={6} key={item.collegeCode}>
                <div className={"item" + (item.active ? ' active' : '')}
                     onClick={() => this.selectData(item.collegeCode, index)}>{item.collegeName}</div>
            </Col>
        ));

        return (
            <MainModal
                title={this.props.title}
                visible={this.props.visible}
                onCancel={this.props.onCancel}
                onOk={this.onOk}
                width={1004}
            >
                <div className={"earlyWarningObjModel"}>
                    <Row gutter={20}>
                        <Checkbox
                            style={{float:"right",padding:"0px 0px 20px 0px"}}
                            indeterminate={this.state.indeterminate}
                            onChange={this.onCheckAllChange}
                            checked={this.state.checkAll}
                        >
                            全选
                        </Checkbox>
                        <CheckboxGroup>{dataLists}</CheckboxGroup>
                    </Row>
                </div>
            </MainModal>
        )
    }
}

export default EarlyWarningObjModel


