import React, { Component } from 'react';
import { Row, Col, InputNumber, Alert, TimePicker } from 'antd';
import ModuleConfigCard from '../../Componets/ModuleConfigCard'
import { isObjectValueEqual } from '../../../../../utils'
import moment from 'moment';
import '../style.less';

let defaultConfig = {
    redVal: null,
    orangeVal: null,
    yellowVal: null,
    blueVal: null,
    startTime: null,
    endTime: null,
    openFlag: 1
}

class NightWarn extends Component {
    state = {
        data: defaultConfig,
        openFlag: true,
        visibleError: false
    }
    componentWillReceiveProps(nextProps) {
        if (!isObjectValueEqual(nextProps.data, this.props.data) && nextProps.data !== null) {
            let data = nextProps.data;
            let openFlag = data.openFlag === 1;
            this.setState({ data, openFlag })
        } else if (nextProps.data === null) {
            this.setState({ openFlag: false })
        }
    }
    componentDidMount() {
        let data = this.props.data;
        let openFlag = data.openFlag === 1;
        this.setState({ data, openFlag })
    }
    updatePropsData(data) {
        if (this.state.openFlag) {
            let a = {
                ...data,
                openFlag: 1
            }
            this.props.onChange(a, 'alarmBackPara', false);
        } else {
            let closeData = {
                redVal: null,
                orangeVal: null,
                yellowVal: null,
                blueVal: null,
                startTime: null,
                endTime: null,
                openFlag: 0
            }
            this.props.onChange(closeData, 'alarmBackPara', true);
        }
    }
    changeValue = (name, value) => {
        let data = this.state.data;
        if (name === 'startTime') {
            data['startTime'] = value.format('HH:mm');
        } else if (name === 'endTime') {
            data['endTime'] = value.format('HH:mm');
        } else {
            data[name] = value;
        }
        this.setState({ data });
        this.updatePropsData(data);
    }
    changeSwitch = () => {
        this.setState({
            openFlag: !this.state.openFlag
        }, () => {
            this.updatePropsData(this.state.data);
        })
    }
    render() {
        let disabled = this.state.openFlag;
        return (
            <ModuleConfigCard
                title={'夜归预警'}
                footer={'学生每月晚于夜归时间，且达到该月晚归/未归次数预警值，系统会触发预警机制。'}
                checked={this.state.openFlag}
                onChange={this.changeSwitch}
            >
                <Row className="nightWarn">
                    <Col span={8} style={{ textAlign: 'center' }}>
                        <div style={{ marginBottom: 10 }}>
                            <span style={{ marginRight: 10 }}>夜归开始时间：</span>

                            <span>
                                <TimePicker style={{ width: 100 }} placeholder={'夜归时间'} format="HH:mm" value={this.state.data.startTime ? moment(this.state.data.startTime, 'HH:mm') : null} onChange={(value) => this.changeValue('startTime', value)} />
                            </span>
                        </div>
                        <div>
                            <span style={{ marginRight: 10 }}>夜归结束时间：</span>
                            <span>
                                <TimePicker style={{ width: 100 }} placeholder={'未归时间'} format="HH:mm" value={this.state.data.endTime ? moment(this.state.data.endTime, 'HH:mm') : null} onChange={(value) => this.changeValue('endTime', value)} />
                            </span>
                        </div>



                    </Col>
                    <Col span={16}>
                        <Row gutter={40}>
                            <Col span={12} className="text-right" style={{ paddingLeft: 10, paddingRight: 10 }}>
                                <span className="warn-cilcle-point"></span>
                                <span style={{ marginRight: 10 }}>红色预警</span>
                                <InputNumber value={this.state.data.redVal} disabled={!disabled}
                                    className="input-item" min={0} onChange={(value) => this.changeValue('redVal', value)} /> 次
                            </Col>
                            <Col span={12} style={{ paddingLeft: 10, paddingRight: 10 }}>
                                <span className="warn-cilcle-point" style={{ backgroundColor: '#f5a623' }}></span>
                                <span style={{ marginRight: 10 }}>橙色预警</span>
                                <InputNumber value={this.state.data.orangeVal} disabled={!disabled}
                                    className="input-item" min={0} onChange={(value) => this.changeValue('orangeVal', value)} /> 次
                            </Col>
                        </Row>
                        <Row gutter={40} style={{ marginTop: 28 }}>
                            <Col span={12} className="text-right" style={{ paddingLeft: 10, paddingRight: 10 }}>
                                <span className="warn-cilcle-point" style={{ backgroundColor: '#f8e71c' }}></span>
                                <span style={{ marginRight: 10 }}>黄色预警</span>
                                <InputNumber value={this.state.data.yellowVal} disabled={!disabled}
                                    className="input-item" min={0} onChange={(value) => this.changeValue('yellowVal', value)} /> 次
                            </Col>
                            <Col span={12} style={{ paddingLeft: 10, paddingRight: 10 }}>
                                <span className="warn-cilcle-point" style={{ backgroundColor: '#309cff' }}></span>
                                <span style={{ marginRight: 10 }}>蓝色预警</span>
                                <InputNumber value={this.state.data.blueVal} disabled={!disabled}
                                    className="input-item" min={0} onChange={(value) => this.changeValue('blueVal', value)} /> 次
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <div style={{ height: 40, margin: '5px 0px', visibility: this.state.visibleError ? 'visible' : 'hidden' }}>
                    <Alert type="error" banner message={'请从大到小按顺序填写'} />
                </div>
            </ModuleConfigCard>
        )
    }
}

export default NightWarn


