import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Card, Button, message } from 'antd';
// import Table from '../../../../components/Table'
import BaseInfo from './BaseInfo'
import ModuleConfig from './ModuleConfig'
import PushConfig from './PushConfig'
import { notNull, ArrayNumSortTure } from '../../../../utils'
import { postAction } from '../../../../axios';

let alarmMissPara = {
    redVal: null,
    orangeVal: null,
    yellowVal: null,
    blueVal: null,
    openFlag: 1
}

let alarmNetworkPara = {
    redVal: null,
    orangeVal: null,
    yellowVal: null,
    blueVal: null,
    openFlag: 1
}

let alarmConsumePara = {
    redMaxVal: null,
    redMinVal: null,
    orangeMaxVal: null,
    orangeMinVal: null,
    yellowMaxVal: null,
    yellowMinVal: null,
    blueMaxVal: null,
    blueMinVal: null,
    openFlag: 1
}

let alarmBackPara = {
    redVal: null,
    orangeVal: null,
    yellowVal: null,
    blueVal: null,
    startTime: null,
    endTime: null,
    openFlag: 1
}

let alarmFailPara = {
    redSubjectVal:null,
    redScoreVal:null,
    orangeSubjectVal:null,
    orangeScoreVal:null,
    yellowSubjectVal:null,
    yellowScoreVal:null,
    blueSubjectVal:null,
    blueScoreVal:null,    
    redRel:'or',
    orangeRel:'or',
    yellowRel:'or',
    blueRel:'or',
    openFlag:1
}

let alarmCoursePara = {
    redVal: null,
    orangeVal: null,
    yellowVal: null,
    blueVal: null,
    openFlag: 0
}

let defaultAlarmStrategyTypeTree = { alarmMissPara, alarmNetworkPara, alarmConsumePara, alarmBackPara, alarmFailPara, alarmCoursePara };
// let defaultAlarmStrategyTypeTree = [outOfContactDefaut,netWorkDefaut,costWarnDefault];

class AddPage extends Component {
    state = {
        pageNum: 1,
        pageSize: 10,
        total: 0,
        strategyName: undefined,//预警策略名称
        strategyTarget: "",//预警策略对象
        startDate: undefined,//预警策略开始时间
        endDate: undefined,//预警策略结束时间
        alarmStrategyType: {},
        alarmPushList: [],//预警推送配置
        alarmStrategyTypeTree: defaultAlarmStrategyTypeTree,//预警模块配置树
        editorId: undefined, //编辑ID
        alarmStrategyTypeIsError: [], //预警模块配置错误列表
        strategyNameIsRepeat: false,
    }
    componentWillMount() {
        this.getData()
    }
    componentWillReceiveProps() {
        alert(1)
    }
    getData() {
        let arr = this.props.location.search.split('?');
        let query = {};
        arr.forEach(a => {
            if (a) {
                let key = a.split("=")[0];
                let val = a.split("=")[1];
                query[key] = val;
            }
        })
        let id = query.id;
        if (id) {
            let data = { id };
            postAction("/bigdata/alarmStrategy/findById", data).then(
                (res) => {
                    if (res.success) {
                        let { strategyName, strategyTarget, startDate, endDate, alarmBackPara, alarmConsumePara, alarmCoursePara, alarmFailPara, alarmMissPara, alarmNetworkPara, alarmPushList } = res.obj;
                        let alarmPushLists = [];
                        alarmPushList.forEach(item => {
                            let { jobNumber, pushName, pushMail, alarmMiss, alarmFail, alarmNetwork, alarmConsume, alarmCourse, alarmBack, mailStatus, msgStatus, redLevel, orangeLevel, yellowLevel, blueLevel, pushPhone } = item;
                            let warnArr = [], warnName, warnModule = [], warnArr2 = [], warnName2, warnModule2 = [];

                            if (alarmMiss) {
                                warnModule.push('alarmMiss');
                                warnArr.push('失联预警');
                            }
                            if (alarmNetwork) {
                                warnModule.push('alarmNetwork');
                                warnArr.push('网络预警');
                            }
                            if (alarmFail) {
                                warnModule.push('alarmFail');
                                warnArr.push('挂科预警');
                            }
                            if (alarmConsume) {
                                warnModule.push('alarmConsume');
                                warnArr.push('消费预警');
                            }
                            if (alarmCourse) {
                                warnModule.push('alarmCourse');
                                warnArr.push('逃课预警');
                            }
                            if (alarmBack) {
                                warnModule.push('alarmBack');
                                warnArr.push('夜归预警');
                            }
                            if (redLevel) {
                                warnModule2.push('redLevel');
                                warnArr2.push('红色预警');
                            }
                            if (orangeLevel) {
                                warnModule2.push('orangeLevel');
                                warnArr2.push('橙色预警');
                            }
                            if (yellowLevel) {
                                warnModule2.push('yellowLevel');
                                warnArr2.push('黄色预警');
                            }
                            if (blueLevel) {
                                warnModule2.push('blueLevel');
                                warnArr2.push('蓝色预警');
                            }
                            warnName = warnArr.join(',');
                            warnName2 = warnArr2.join(',');
                            let data = { pushPhone, jobNumber, pushName, pushMail, alarmMiss, alarmFail, alarmNetwork, alarmConsume, alarmCourse, alarmBack, warnName, warnName2, warnModule, warnModule2, mailStatus, msgStatus, redLevel, orangeLevel, yellowLevel, blueLevel }
                            alarmPushLists.push(data)
                        })
                        this.setState({
                            showDetail: true,
                            strategyName, strategyTarget, startDate, endDate,
                            alarmStrategyTypeTree: { alarmBackPara, alarmConsumePara, alarmCoursePara, alarmFailPara, alarmMissPara, alarmNetworkPara },
                            alarmPushList: alarmPushLists || [],
                            editorId: id
                        })
                    } else {
                        message.warn(res.obj);
                    }
                }
            )
        }
    }
    changeState = (state) => {
        this.setState(state);
    }
    changePage = (page) => {
        this.setState({ pageNum: page }, () => this.getData())
    }
    checkCommonModule = (themodule, modulename,tag) => {
        let tmp;
        // 首先判断模块是否开启，开启了才需要校验
        if (themodule.openFlag) {  
            if(tag){
                tmp = [
                    themodule.blueVal,
                    themodule.yellowVal,
                    themodule.orangeVal,
                    themodule.redVal]
            }else{
                tmp = [
                    themodule.redVal,
                    themodule.orangeVal,
                    themodule.yellowVal,
                    themodule.blueVal]
            }             
            // 过滤掉非数字
            tmp=tmp.filter(item=>{
                if(item){
                    return true
                }else{
                    return false
                }
            })
            if(tmp.length === 0){
                message.error(`请配置${modulename}模块！`);
                return false;
            }
            // 比对数组顺序是否正确            
            let isSort = ArrayNumSortTure(tmp)
            if (!isSort) {
                if(tag){
                    message.error(`请按从小到大的顺序配置${modulename}模块！`);
                }else{
                    message.error(`请按从大到小的顺序配置${modulename}模块！`);
                }                
                return false;
            } else {
                return true
            }  
        } else {
            return true
        }
    }    
    checkComsume = (themodule) => {
        // 首先判断模块是否开启，开启了才需要校验
        if (themodule.openFlag) {            
            let tmpMax = [
                themodule.redMaxVal,
                themodule.orangeMaxVal,
                themodule.yellowMaxVal,
                themodule.blueMaxVal]
            // 检查每个字段高于数值不能小于低于数值
            if (themodule.redMaxVal < themodule.redMinVal) {
                message.error(`消费预警模块的红色预警高于数值不能小于低于数值！`);
                return false;
            }
            if (themodule.orangeMaxVal < themodule.orangeMinVal) {
                message.error(`消费预警模块的橙色预警高于数值不能小于低于数值！`);
                return false;
            }
            if (themodule.yellowMaxVal < themodule.yellowMinVal) {
                message.error(`消费预警模块的黄色预警高于数值不能小于低于数值！`);
                return false;
            }
            if (themodule.blueMaxVal < themodule.blueMinVal) {
                message.error(`消费预警模块的蓝色预警高于数值不能小于低于数值！`);
                return false;
            }
            // 过滤掉非数字
            tmpMax=tmpMax.filter(item=>{
                if(item){
                    return true
                }else{
                    return false
                }
            })
            if(tmpMax.length === 0){
                message.error(`请配置消费预警模块！`);
                return false;
            }
            // 比对数组顺序是否正确
            let isSort = ArrayNumSortTure(tmpMax)
            if (!isSort) {
                message.error(`请按从大到小的顺序配置消费预警模块！`);
                return false;
            } else {
                return true
            }            
        } else {
            return true
        }
    }
    // 校验挂科预警模块
    checkGuake=(themodule)=>{
        if(themodule.openFlag){
            // 首先判断或且的逻辑，检查当开关为且的时候，两个数值必须有值，其他条件不管
            if(themodule.redRel === 'and'){
                if(themodule.redSubjectVal && themodule.redScoreVal){
                }else{
                    message.warning('挂科预警-红色预警数值不能为空')
                    return false
                }
            }
            if(themodule.orangeRel === 'and'){
                if(themodule.orangeSubjectVal && themodule.orangeScoreVal){
                }else{
                    message.warning('挂科预警-橙色预警数值不能为空')
                    return false
                }
            }
            if(themodule.yellowRel === 'and'){
                if(themodule.yellowSubjectVal && themodule.yellowScoreVal){
                }else{
                    message.warning('挂科预警-黄色预警数值不能为空')
                    return false
                }
            }
            if(themodule.blueRel === 'and'){
                if(themodule.blueSubjectVal && themodule.blueScoreVal){
                }else{
                    message.warning('挂科预警-蓝色预警数值不能为空')
                    return false
                }
            }
            // 拿到挂科数量的数组和学分数量的数组
            let subjectArr=[
                themodule.redSubjectVal,
                themodule.orangeSubjectVal,
                themodule.yellowSubjectVal,
                themodule.blueSubjectVal
            ],scoreArr=[
                themodule.blueScoreVal,
                themodule.yellowScoreVal,
                themodule.orangeScoreVal,
                themodule.redScoreVal
            ]
            // 过滤空值
            subjectArr=subjectArr.filter(item=>{
                if(item){
                    return true
                }else{
                    return false
                }
            })
            scoreArr=scoreArr.filter(item=>{
                if(item){
                    return true
                }else{
                    return false
                }
            })
            // 判断两组数值是否都为空
            if(subjectArr.length === 0 && scoreArr.length === 0){
                message.error(`请配置挂科预警模块！`);
                return false;
            }
            let sortSubject=ArrayNumSortTure(subjectArr)
            let sortScore=ArrayNumSortTure(scoreArr)
            if (!sortSubject) {
                message.error(`请按从大到小的顺序配置挂科预警-挂科数！`);                            
                return false;
            } 
            if (!sortScore) {
                message.error(`请按从小到大的顺序配置挂科预警-学分！`);                            
                return false;
            } 

            return true
        }else{
            return true
        }
    }
    saveAction = () => {
        let { strategyName, strategyTarget, startDate, endDate, alarmStrategyTypeTree, alarmPushList, strategyNameIsRepeat } = this.state;
        if (alarmPushList.length > 0 && !alarmPushList[0].mailStatus && !alarmPushList[0].msgStatus) {
            message.warning('请选择至少一种推送方式')
            return
        } else if (alarmPushList.length > 0 && alarmPushList[0].mailStatus === 0 && alarmPushList[0].msgStatus === 0) {
            message.warning('请选择至少一种推送方式')
            return
        }

        if (!notNull(strategyName)) {
            message.error('预警策略名称不能为空！'); return;
        }
        if (strategyNameIsRepeat) {
            message.error('预警策略名称不能重复！'); return;
        }
        if (!notNull(strategyTarget)) {
            message.error('预警策略对象不能为空！'); return;
        }
        if (!notNull(startDate) && !notNull(endDate)) {
            message.error('预警策略时间不能为空！'); return;
        }
        // if(alarmStrategyType.length>0){
        //     for(let i=0;i<alarmStrategyType.length;i++){
        //         let item = alarmStrategyType[i];
        //         if(item.type===1 || item.type===2){
        //             if(!item.minVal){
        //                 message.error('预警模块配置参数未填写！');
        //                 return;
        //             }
        //         } 
        //         if(item.type===4){
        //             if(!item.maxVal){
        //                 message.error('预警模块配置参数未填写！');
        //                 return;
        //             }
        //         }
        //     }
        //     if(this.state.alarmStrategyTypeIsError.some(item=>item===false)){
        //         message.error('预警模块配置参数错误！');
        //         return;
        //     }
        // }
        if (alarmPushList.length === 0) {
            message.error('预警推送人列表不能为空！'); return;
        }
        let data = { strategyName, strategyTarget, startDate, endDate, ...alarmStrategyTypeTree, alarmPushList };
        // 逐个校验模块
        // 失联预警
        // console.log(data)
        let a = this.checkCommonModule(data.alarmMissPara, '失联预警')
        let b = this.checkCommonModule(data.alarmNetworkPara, '网络预警')
        let c = this.checkGuake(data.alarmFailPara)
        let d = this.checkCommonModule(data.alarmCoursePara, '逃课预警',true)
        let e = this.checkCommonModule(data.alarmBackPara, '夜归预警')
        let f = this.checkComsume(data.alarmConsumePara)
        if (a && b && c && d && e && f) {
            if (this.state.editorId) {
                data.id = this.state.editorId;
                //修改
                postAction("/bigdata/alarmStrategy/update", data).then(
                    (res) => {
                        if (res.success) {
                            message.success('保存成功！', 2, () => {
                                this.props.history.goBack();
                            });
                        } else {
                            message.warn(res.obj);
                        }
                    }
                )
            } else {
                //新增
                postAction("/bigdata/alarmStrategy/save", data).then(
                    (res) => {
                        if (res.success) {
                            message.success('保存成功！', 2, () => {
                                this.props.history.goBack();
                            });
                        } else {
                            message.warn(res.obj);
                        }
                    }
                )
            }
        }


    }
    render() {
        const dataSource = this.state.alarmPushList || [];
        dataSource.forEach((item, index) => {
            dataSource[index].key = index;
            dataSource[index].index = index + 1;
        })
        let total = dataSource.length;
        
        return (
            <React.Fragment>
                <BaseInfo
                    strategyName={this.state.strategyName}
                    strategyTarget={this.state.strategyTarget}
                    startDate={this.state.startDate}
                    endDate={this.state.endDate}
                    onChange={this.changeState}
                    strategyNameIsRepeat={this.state.strategyNameIsRepeat}
                    editorId={this.state.editorId}
                />
                <ModuleConfig style={{ marginTop: 20 }}
                    alarmStrategyType={this.state.alarmStrategyType}
                    alarmStrategyTypeTree={this.state.alarmStrategyTypeTree}
                    alarmStrategyTypeIsError={this.state.alarmStrategyTypeIsError}
                    onChange={this.changeState} />
                <PushConfig style={{ marginTop: 20 }} editorId={this.state.editorId} dataSource={dataSource} total={total} onChange={this.changeState} />
                <Card style={{ marginTop: 20 }}>
                    <div style={{ margin: '-15px 0', textAlign: 'right' }}>
                        <Button size="large" type="primary" style={{ marginRight: 20 }} onClick={this.saveAction}>保存</Button>
                        <Button size="large" onClick={() => {
                            this.props.history.go(-1)
                        }}>返回</Button>
                    </div>
                </Card>
            </React.Fragment>
        )
    }
}

export default withRouter(AddPage);


