import React, { Component } from 'react';
import { Row, Col, InputNumber, Alert, Switch } from 'antd';
import ModuleConfigCard from '../../Componets/ModuleConfigCard'
import { isObjectValueEqual } from '../../../../../utils'

let defaultConfig = {
    redSubjectVal:null,
    redScoreVal:null,
    orangeSubjectVal:null,
    orangeScoreVal:null,
    yellowSubjectVal:null,
    yellowScoreVal:null,
    blueSubjectVal:null,
    blueScoreVal:null,    
    redRel:'or',
    orangeRel:'or',
    yellowRel:'or',
    blueRel:'or',
    openFlag:1
}

class FailWarn extends Component {
    state = {
        data: defaultConfig,
        openFlag: true,
        visibleError: false,
        errorText: '请从大到小按顺序填写'
    }
    componentWillReceiveProps(nextProps){
        if(!isObjectValueEqual(nextProps.data,this.props.data)&&nextProps.data!==null){
            let data = nextProps.data;
            let openFlag = data.openFlag===1;
            this.setState({data,openFlag})
        } else if(nextProps.data===null) {
            this.setState({openFlag:false})
        }
    }
    updatePropsData(data){
        if(this.state.openFlag){
            let a={
                ...data,
                openFlag:1
            }
            this.props.onChange(a,'alarmFailPara',false);
        } else {
            let closeData = {
                redSubjectVal:null,
                redScoreVal:null,
                orangeSubjectVal:null,
                orangeScoreVal:null,
                yellowSubjectVal:null,
                yellowScoreVal:null,
                blueSubjectVal:null,
                blueScoreVal:null,  
                redRel:'or',
                orangeRel:'or',
                yellowRel:'or',
                blueRel:'or',
                openFlag:0
            }
            this.props.onChange(closeData,'alarmFailPara',true);
        }
    }
    changeValue = (name,value)=> {
        let data = this.state.data;
        data[name] = value;
        this.setState({data});
        this.updatePropsData(data);
    }
    changeSwitch = ()=> {
        this.setState({
            openFlag:!this.state.openFlag
        },()=>{
            this.updatePropsData(this.state.data);
        })
    }
    handleSwitch(a,b){
        let data={...this.state.data,[a]:b ? 'or':'and'}
        this.setState({
            data
        })
        this.updatePropsData(data);
    }
    render() {
        let disabled = this.state.openFlag;
        return (
            <ModuleConfigCard
                title={'挂科预警'}
                footer={'学生入学以来累计挂科数量达到预警值，系统会触发预警机制。'}
                checked={this.state.openFlag}
                onChange={this.changeSwitch}
            >
                <Row gutter={40}>
                    <Col xs={4} sm={8} md={12} className="text-right">
                        <span className="warn-cilcle-point"></span>
                        <span className="text-title" style={{ marginRight: 10 }}>红色预警</span>
                        <InputNumber value={this.state.data.redSubjectVal} disabled={!disabled}
                                     className="input-item" min={0} onChange={(value)=>this.changeValue('redSubjectVal',value)} /> 科
                        <Switch onChange={this.handleSwitch.bind(this,'redRel')} checkedChildren="或" unCheckedChildren="且" checked={this.state.data.redRel === 'or'} size="small" style={{margin:"0px 10px"}} disabled={!disabled} />
                        <InputNumber value={this.state.data.redScoreVal} disabled={!disabled}
                                     className="input-item" min={0} onChange={(value)=>this.changeValue('redScoreVal',value)} /> 学分
                    </Col>
                    <Col xs={4} sm={8} md={12}>
                        <span className="warn-cilcle-point" style={{ backgroundColor: '#f5a623' }}></span>
                        <span style={{ marginRight: 10 }}>橙色预警</span>
                        <InputNumber value={this.state.data.orangeSubjectVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('orangeSubjectVal',value)} /> 科
                        <Switch onChange={this.handleSwitch.bind(this,'orangeRel')} checkedChildren="或" unCheckedChildren="且"  checked={this.state.data.orangeRel === 'or'} size="small" style={{margin:"0px 10px"}} disabled={!disabled} />
                        <InputNumber value={this.state.data.orangeScoreVal} disabled={!disabled}
                                     className="input-item" min={0} onChange={(value)=>this.changeValue('orangeScoreVal',value)} /> 学分
                    </Col>
                </Row>
                <Row gutter={40} style={{ marginTop: 28 }}>
                    <Col xs={4} sm={8} md={12} className="text-right">
                        <span className="warn-cilcle-point" style={{ backgroundColor: '#f8e71c' }}></span>
                        <span style={{ marginRight: 10 }}>黄色预警</span>
                        <InputNumber value={this.state.data.yellowSubjectVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('yellowSubjectVal',value)} /> 科
                        <Switch onChange={this.handleSwitch.bind(this,'yellowRel')} checkedChildren="或" unCheckedChildren="且"  checked={this.state.data.yellowRel === 'or'} size="small" style={{margin:"0px 10px"}} disabled={!disabled} />
                        <InputNumber value={this.state.data.yellowScoreVal} disabled={!disabled}
                                     className="input-item" min={0} onChange={(value)=>this.changeValue('yellowScoreVal',value)} /> 学分
                    </Col>
                    <Col xs={4} sm={8} md={12}>
                        <span className="warn-cilcle-point" style={{ backgroundColor: '#309cff' }}></span>
                        <span style={{ marginRight: 10 }}>蓝色预警</span>
                        <InputNumber value={this.state.data.blueSubjectVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('blueSubjectVal',value)} /> 科
                        <Switch onChange={this.handleSwitch.bind(this,'blueRel')} checkedChildren="或" unCheckedChildren="且"  checked={this.state.data.blueRel === 'or'} size="small" style={{margin:"0px 10px"}} disabled={!disabled} />
                        <InputNumber value={this.state.data.blueScoreVal} disabled={!disabled}
                                     className="input-item" min={0} onChange={(value)=>this.changeValue('blueScoreVal',value)} /> 学分
                    </Col>
                </Row>
                <div style={{ height:40,margin:'5px 0px',visibility:this.state.visibleError?'visible':'hidden' }}>
                    <Alert type="error" banner message={this.state.errorText} />
                </div>
            </ModuleConfigCard>
        )
    }
}

export default FailWarn


