import React, { Component } from 'react';
import { Row, Col, InputNumber, Alert } from 'antd';
import ModuleConfigCard from '../../Componets/ModuleConfigCard'
import { isObjectValueEqual, ArrayNumSortTure } from '../../../../../utils'

let defaultConfig = {
    redMaxVal:null,
    redMinVal:null,
    orangeMaxVal:null,
    orangeMinVal:null,
    yellowMaxVal:null,
    yellowMinVal:null,
    blueMaxVal:null,
    blueMinVal:null,
    openFlag:1
}

class CostWarn extends Component {
    state = {
        data: defaultConfig,
        openFlag: true,
        visibleError: false,
        errorText: '请从大到小按顺序填写'
    }
    componentWillReceiveProps(nextProps){
        if(!isObjectValueEqual(nextProps.data,this.props.data)&&nextProps.data!==null){
            let data = nextProps.data;
            let openFlag = data.openFlag===1;
            this.setState({data,openFlag})
        } else if(nextProps.data===null) {
            this.setState({openFlag:false})
        }
    }
    updatePropsData(data){
        if(this.state.openFlag){
            let a={
                ...data,
                openFlag:1
            }
            this.props.onChange(a,'alarmConsumePara',false);
        } else {
            let closeData = {
                redMaxVal:null,
                redMinVal:null,
                orangeMaxVal:null,
                orangeMinVal:null,
                yellowMaxVal:null,
                yellowMinVal:null,
                blueMaxVal:null,
                blueMinVal:null,
                openFlag:0
            }
            this.props.onChange(closeData,'alarmConsumePara',true);
        }
    }
    checkValue = (data)=> {
        let dataArr = data.map(item=>item.minVal);
        let maxDataArr = data.map(item=>item.maxVal);
        let flag = !ArrayNumSortTure(dataArr,true) || !ArrayNumSortTure(maxDataArr);
        let errorText;
        if(!ArrayNumSortTure(dataArr,true)){
            errorText = '月消费最低金额请从小到大按顺序填写';
        } else {
            errorText = '月消费最高金额请从大到小按顺序填写';
        }
        data.forEach(item => {
            if(item.minVal && item.maxVal && item.minVal>=item.maxVal){
                flag = true;
                errorText = '月消费最低金额不能大于月消费最高金额';
            }
        });
        this.setState({visibleError:flag,errorText})
        return !flag;
    }
    changeValue = (name,value)=> {
        let data = this.state.data;
        data[name] = value;
        this.setState({data});
        this.updatePropsData(data);
    }
    changeSwitch = ()=> {
        this.setState({
            openFlag:!this.state.openFlag
        },()=>{
            this.updatePropsData(this.state.data);
        })
    }
    render() {
        let disabled = this.state.openFlag;
        return (
            <ModuleConfigCard
                title={'消费预警'}
                footer={'学生当月日均消费低于/高于预警值，系统会触发预警机制。'}
                checked={this.state.openFlag}
                onChange={this.changeSwitch}
            >
                <Row gutter={40}>
                    <Col span={12} className="text-right">
                        <span className="warn-cilcle-point"></span>
                        <span style={{ marginRight: 10 }}>红色预警</span>
                        <span style={{margin: '0px 5px'}}>高于</span>
                        <InputNumber value={this.state.data.redMaxVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('redMaxVal',value)} />
                            <span> —— </span>
                        <span style={{margin: '0px 5px'}}>低于</span>
                        <InputNumber value={this.state.data.redMinVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('redMinVal',value)} /> 元
                    </Col>
                    <Col span={12}>
                        <span className="warn-cilcle-point" style={{ backgroundColor: '#f5a623' }}></span>
                        <span style={{ marginRight: 10 }}>橙色预警</span>
                        <span style={{margin: '0px 5px'}}>高于</span>
                        <InputNumber value={this.state.data.orangeMaxVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('orangeMaxVal',value)} />
                            <span> —— </span>
                            <span style={{margin: '0px 5px'}}>低于</span>
                        <InputNumber value={this.state.data.orangeMinVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('orangeMinVal',value)} /> 元
                    </Col>
                </Row>
                <Row gutter={40} style={{ marginTop: 28 }}>
                    <Col span={12} className="text-right">
                        <span className="warn-cilcle-point" style={{ backgroundColor: '#f8e71c' }}></span>
                        <span style={{ marginRight: 10 }}>黄色预警</span>
                        <span style={{margin: '0px 5px'}}>高于</span>
                        <InputNumber value={this.state.data.yellowMaxVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('yellowMaxVal',value)} />
                            <span> —— </span>
                            <span style={{margin: '0px 5px'}}>低于</span>
                        <InputNumber value={this.state.data.yellowMinVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('yellowMinVal',value)} /> 元
                    </Col>
                    <Col span={12}>
                        <span className="warn-cilcle-point" style={{ backgroundColor: '#309cff' }}></span>
                        <span style={{ marginRight: 10 }}>蓝色预警</span>
                        <span style={{margin: '0px 5px'}}>高于</span>
                        <InputNumber value={this.state.data.blueMaxVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('blueMaxVal',value)} />
                            <span> —— </span>
                            <span style={{margin: '0px 5px'}}>低于</span>
                        <InputNumber value={this.state.data.blueMinVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('blueMinVal',value)} /> 元
                    </Col>
                </Row>
                <div style={{ height:40,margin:'5px 0px',visibility:this.state.visibleError?'visible':'hidden' }}>
                    <Alert type="error" banner message={this.state.errorText} />
                </div>
            </ModuleConfigCard>
        )
    }
}

export default CostWarn


