import React, { Component } from 'react'
import { Form, Input, Checkbox,Row,Col,Divider,Tree,Button,Tag ,message } from 'antd'
import MainModal from '../../../../../common/components/MainModal'
import './pushconfig.less';
// import {getLocal} from '../../../../../utils'
import {getAction} from '../../../../../axios'
const FormItem = Form.Item;
const TreeNode = Tree.TreeNode;
const formItemLayout = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18, style:{paddingLeft:'10px'}},
};

class AddOrEditorModel extends Component {
    state={
        searchWord:'',
        treeData:[],
        pushPerson:[],
        selectedKeys:[],
        editData:{},
        expandKeys:[]
    }
    onOk = ()=> {
        this.props.form.validateFields(
            (err,values) => {
                if (!err) {         
                    let data=this.state.pushPerson.map(item=>{
                        item={
                            ...item,
                            ...values
                        }
                        return item
                    })
                    if(this.state.pushPerson.length>0){
                        // 给定一个参数方便父组件识别是哪种模式
                        if(this.props.isEdit){
                            this.props.onOk({values,pushPerson:data},true);
                        }else{
                            this.props.onOk({values,pushPerson:data});
                        }
                        
                    }else{
                        message.error('请选择预警接受人员')
                    }                    
                }
            },
        );
    }
    getTreeData=()=>{
        getAction(
            '/bigdata/alarmStrategy/getTeacherTreeData',
            {}
        ).then(res=>{
            if(res.success){                
                this.setState({
                    treeData:res.obj.children
                })                
                
            }
        })
    }
    componentDidMount = () => {
        // 根据默认带过来的推送人员渲染弹窗           
        this.getTreeData()
        if(this.props.isEdit){
            // 生成展示的名称
            let person={
                jobNumber:this.props.editData.jobNumber,
                pushName:this.props.editData.pushName,
                pushPhone:this.props.editData.pushPhone,
                pushMail:this.props.editData.pushMail,
            }
            this.setState({
                editData:this.props.editData,
                pushPerson:[person]
            })
        }
          
    }
    
    // 处理树形点击
    handleSelectNode=(value,node)=>{
        let originList=this.props.dataLists,tmplist;
        let tmp=[]
        if(value.length>0){
            tmp=value.map(item=>{
                let splitArr=item.split(',')
                tmplist=originList.filter(item=>item.jobNumber !== splitArr[0])
                return {
                    jobNumber:splitArr[0] || '',
                    pushName:splitArr[1] || '',
                    pushPhone:splitArr[2] || '',
                    pushMail:splitArr[3] || '',
                }
            })
        }
        if(tmplist.length<originList.length){
            message.error('预警接受人员不可重复')
            return
        }
        this.setState({
            pushPerson:tmp,
            selectedKeys:value
        })   
    }   
    // 删除选择的推送人员
    handleDelete(item,e){
        // 根据点击取到的id去删除selectkeys和pushperson
        let id=item.jobNumber;
        // 拷贝之前的state数据一份
        let deepKeys=[...this.state.selectedKeys],deepPerson=JSON.parse(JSON.stringify(this.state.pushPerson));
        // 过滤数据
        deepKeys=deepKeys.filter(item=>{
            if(item.split(',')[0] === id){
                return false
            }else{
                return true
            }
        })
        deepPerson=deepPerson.filter(item=>item.jobNumber !==id )
        this.setState({
            selectedKeys:deepKeys,
            pushPerson:deepPerson
        })
        
    }
    handlePermission=()=>{
        if(this.props.isEdit){
            message.error('编辑模式下不支持修改人员信息')
        }
    }
    handleInputChange=(e)=>{
        this.setState({
            searchWord:e.target.value
        })
    }
    // 根据关键字过滤树节点设置展开节点
    handleExpand=(data,arr,keyWord)=>{
        data.forEach(item=>{
            if(item.children && item.children.length>0){
                let str=JSON.stringify(item.children)
                if(str.indexOf(keyWord)>-1){
                    arr.push(item.key)
                }
                this.handleExpand(item.children,arr,keyWord)
            }
        })
    }
    handleSearchTree=()=>{
        let keys=[]
        this.handleExpand(this.state.treeData,keys,this.state.searchWord)
        // console.log(keys)
        this.setState({
            expandKeys:keys
        })
    }
    expand=(keys)=> {
        this.setState({
            expandKeys: keys
        })
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        const {treeData,pushPerson,editData,expandKeys}=this.state        
        let personNum=pushPerson.length || 0
        //递归菜单列表
		const loop = data => data.map((item) => {
			if (item.children && item.children.length) {
                if(item.key.indexOf(',')>0){
                    return <TreeNode key={item.key} title={item.title} detail={item}>{loop(item.children)}</TreeNode>;
                }else{
                    return <TreeNode disabled key={item.key} title={item.title} detail={item}>{loop(item.children)}</TreeNode>;
                }
				
            }
            if(item.key.indexOf(',')>0){
                return <TreeNode key={item.key} title={item.title} detail={item}/>;
            }else{
                return <TreeNode disabled key={item.key} title={item.title} detail={item}/>;
            }
			
		});
        return (
            <MainModal
                title={this.props.title}
                visible={this.props.visible}
                onCancel={this.props.onCancel}
                onOk={this.onOk}
                width={600}
            >
                <div className="addOrEditorModel">
                    <Row gutter={10}>
                        <Col span={12} style={{borderRight:'1px solid #d3d3d3'}}>
                        <div>
                            <div>
                                预警接受人员：共{personNum}人
                            </div>
                            <div style={{height:200,overflow:'auto',padding:'10px 0',boxSizing:'border-box'}}>
                                {
                                    pushPerson.length && pushPerson.length>0 ?
                                    pushPerson.map(item=><Tag onClose={this.handleDelete.bind(this,item)} style={{marginBottom:5}} key={item.jobNumber}  closable={!this.props.isEdit}>{item.pushName}</Tag>) :null
                                }
                                
                            </div>
                        </div>
                        <FormItem {...formItemLayout} label="提醒模块" style={{marginBottom:0}}>
                        {getFieldDecorator('warnModule', {
                            rules: [{
                                required: true,
                                message: '请选择提醒模块！',
                                type: 'array'
                            }],
                            initialValue: editData?editData.warnModule:[]
                        })(
                            <Checkbox.Group>
                             <Row>
                                 {this.props.warnModules.map((item,index)=><Col span={12} key={index}><Checkbox value={item.value}>{item.label}</Checkbox></Col>)}
                                </Row>
                            </Checkbox.Group>
                        )}
                        
                    </FormItem>
                    <FormItem {...formItemLayout} label="关联级别">
                        {getFieldDecorator('warnModule2', {
                            rules: [{
                                required: true,
                                message: '请选择提醒模块！',
                                type: 'array'
                            }],
                            initialValue: editData?editData.warnModule2:[]
                        })(
                            <Checkbox.Group>
                             <Row>
                                 {this.props.warnModules2.map((item,index)=><Col span={12} key={index}><Checkbox value={item.value}>{item.label}</Checkbox></Col>)}
                                </Row>
                            </Checkbox.Group>
                        )}
                        
                    </FormItem>
                        </Col>
                        <Divider type='vertical'></Divider>
                        <Col span={12}>
                            <div onClick={this.handlePermission} style={{display:'flex',marginBottom:10}}>
                                <Input onChange={this.handleInputChange} style={{flex:1,marginRight:20}}></Input>
                                <Button onClick={this.handleSearchTree} type='primary'>查询</Button>
                            </div>
                            <div onClick={this.handlePermission} style={{height:350,overflow:'auto'}}>
                                <Tree 
                                    multiple
                                    onSelect={this.handleSelectNode}
                                    selectedKeys={this.state.selectedKeys}
                                    disabled={this.props.isEdit}
                                    expandedKeys={expandKeys}
                                    onExpand={this.expand}
                                >
                                    {
                                        loop(treeData)
                                    }

                                </Tree>
                            </div>
                            
                        </Col>
                    </Row>
                    
                </div>
            </MainModal>
        )
    }
}
const AddOrEditorModelForm = Form.create()(AddOrEditorModel);
export default AddOrEditorModelForm;


