import React, { Component } from 'react';
import { Row, Col, InputNumber, Alert } from 'antd';
import ModuleConfigCard from '../../Componets/ModuleConfigCard'
import { isObjectValueEqual } from '../../../../../utils'

let defaultConfig = {
	redVal:null,
    orangeVal:null,
    yellowVal:null,
    blueVal:null,
    openFlag:1
}

class OutOfContact extends Component {
    state = {
        data: defaultConfig,
        openFlag: true,
        visibleError: false
    }
    componentWillReceiveProps(nextProps){
        if(!isObjectValueEqual(nextProps.data,this.props.data)&&nextProps.data!==null){
            let data = nextProps.data;
            let openFlag = data.openFlag===1;
            this.setState({data,openFlag})
        } else if(nextProps.data===null) {
            this.setState({openFlag:false})
        }
    }
    updatePropsData(data){
        if(this.state.openFlag){
            let a={
                ...data,
                openFlag:1
            }
            this.props.onChange(a,'alarmMissPara',false);
        } else {
            let closeData = {
                redVal:null,
                orangeVal:null,
                yellowVal:null,
                blueVal:null,
                openFlag:0
            }
            this.props.onChange(closeData,'alarmMissPara',true);
        }
    }
    changeValue = (name,value)=> {
        let data = this.state.data;
        data[name] = value;
        this.setState({data});
        this.updatePropsData(data);
    }
    changeSwitch = ()=> {
        this.setState({
            openFlag:!this.state.openFlag
        },()=>{
            this.updatePropsData(this.state.data);
        })
    }
    render() {
        let disabled = this.state.openFlag;
        return (
            <ModuleConfigCard
                title={'失联预警'}
                footer={'学生连续失联天数达到预警值，系统会触发预警机制。'}
                checked={this.state.openFlag}
                onChange={this.changeSwitch}
            >
                <Row gutter={40}>
                    <Col span={12} className="text-right">
                        <span className="warn-cilcle-point"></span>
                        <span style={{ marginRight: 10 }}>红色预警</span>
                        <InputNumber value={this.state.data.redVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('redVal',value)} /> 天
                    </Col>
                    <Col span={12}>
                        <span className="warn-cilcle-point" style={{ backgroundColor: '#f5a623' }}></span>
                        <span style={{ marginRight: 10 }}>橙色预警</span>
                        <InputNumber value={this.state.data.orangeVal} disabled={!disabled}
                            className="input-item" min={0} onChange={(value)=>this.changeValue('orangeVal',value)}  /> 天
                    </Col>
                </Row>
                <Row gutter={40} style={{ marginTop: 28 }}>
                    <Col span={12} className="text-right">
                        <span className="warn-cilcle-point" style={{ backgroundColor: '#f8e71c' }}></span>
                        <span style={{ marginRight: 10 }}>黄色预警</span>
                        <InputNumber value={this.state.data.yellowVal} disabled={!disabled} 
                            className="input-item" min={0} onChange={(value)=>this.changeValue('yellowVal',value)}  /> 天
                    </Col>
                    <Col span={12}>
                        <span className="warn-cilcle-point" style={{ backgroundColor: '#309cff' }}></span>
                        <span style={{ marginRight: 10 }}>蓝色预警</span>
                        <InputNumber value={this.state.data.blueVal} disabled={!disabled} 
                            className="input-item" min={0} onChange={(value)=>this.changeValue('blueVal',value)}  /> 天
                    </Col>
                </Row>
                <div style={{ height:40,margin:'5px 0px',visibility:this.state.visibleError?'visible':'hidden' }}>
                    <Alert type="error" banner message={'请从大到小按顺序填写'} />
                </div>
            </ModuleConfigCard>
        )
    }
}

export default OutOfContact


